.slider {
  font-family:
    "poppins",
    "Arial",
    sans-serif;
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 500;
  color: $dcg-2;

  // @include column-flex;
  // width: 100%;
  // position: relative;

  figure {
    @include zero-edges;
    @include column-flex;
    width: 100%;
  }

  img {
    @include content-in-padded-box;
  }

  .swiper-pagination-bullet {
    color: $lcg-5;

    padding: 0;
    border-radius: 0;

    &:focus,
    &:hover {
      color: $mcg-1;
      transform: scale(1.1);
    }

    &:not(:last-child) {
      margin-right: 12px;
    }

    &:last-child {
      margin-right: 0;
      margin-left: 0;
    }
  }

  .swiper-pagination-bullet-active {
    color: $dcg-2;
    font-weight: 700;

    &:focus,
    &:hover {
      color: $mcg-1;
    }
  }

  &--catalog {
    margin-bottom: 76px;

    @media (max-width: $pre-desktop-width) {
      margin-bottom: 74px;
    }

    @media (max-width: $pre-tablet-width) {
      margin-bottom: 64px;
    }

    .swiper-pagination-bullet {
      &:not(:last-child) {
        margin-right: 14px;

        @media (max-width: $pre-tablet-width) {
          margin-right: 23px;
        }
      }
    }
  }
}

.slider__inner {
  @media (min-width: $desktop-width) {
    margin-bottom: 50px;
  }

  @media (max-width: $pre-desktop-width) {
    margin-bottom: 41px;
  }

  @media (max-width: $pre-tablet-width) {
    margin-bottom: 32px;
  }

  &--catalog {
    margin-bottom: 71px;

    @media (max-width: $pre-tablet-width) {
      margin-bottom: 42px;
    }
  }
}

