.slider__padded-box {
  @include padded-box;
  padding-bottom: $ratio-slider-desktop;
  margin-bottom: 23px;

  @media (max-width: $pre-desktop-width) {
    padding-bottom: $ratio-slider-tablet;
    margin-bottom: 23px;
  }

  @media (max-width: $pre-tablet-width) {
    padding-bottom: $ratio-slider-mobile;
    margin-bottom: 17px;
  }

  &--catalog {
    @media (min-width: $desktop-width) {
      margin-bottom: 10px;
    }

    @media (max-width: $pre-desktop-width) {
      margin-bottom: 15px;
    }

    @media (max-width: $pre-tablet-width) {
      margin-bottom: 18px;
    }
  }
}

.slider__figcaption {
  @include column-flex;
}

.slider__product-name {
  text-align: center;
  margin-bottom: 5px;

  &--shifted {
    @media (max-width: $pre-desktop-width) {
      transform: translate(8px, 0);
    }

    @media (max-width: $pre-tablet-width) {
      transform: translate(0, 0);
    }
  }

  &--altered {
    @media (min-width: $desktop-width) {
      transform: translate(5px, 0);
    }
  }
}

.slider__product-cost {
  font-size: 1.4rem;
  font-weight: 700;

  &--altered {
    @media (min-width: $desktop-width) {
      transform: translate(5px, 0);
    }
  }
}

.slider__arrow {
  --icon-color: #{$mcg-1};

  @include zero-edges;
  @include centering-flex;

  position: absolute;
  top: 0;
  touch-action: manipulation;

  &--previous {
    left: 0;
    transform: translate(0, -103px);

    @media (max-width: $pre-desktop-width) {
      transform: translate(0, -97px);
    }
  }

  &--next {
    left: 100%;
    transform: translate(-100%, -103px);

    @media (max-width: $pre-desktop-width) {
      transform: translate(-100%, -97px);
    }
  }

  &--catalog {
    font-size: 1.6rem;
    line-height: 130%;
    font-weight: 500;

    top: 100%;
    transform: translate(0, -100%);
  }

  &--backward {
    left: 0;
    transform: translate(0, -100%);
  }

  &--forward {
    left: 100%;
    transform: translate(-100%, -100%);
  }

  &:hover,
  &:focus {
    --icon-color: #{$dcg-1};

    color: $mcg-1;

    .slider__arrow-svg {
      transform: scale(1.1);

      &--previous {
        transform: rotate(180deg) scale(1.1);
      }
    }
  }

  &:active {
    opacity: 0.5;
  }

  &:disabled {
    cursor: not-allowed;
  }
}

.slider__arrow-svg {
  @media (max-width: $pre-desktop-width) {
    width: 36px;
    height: 15px;
  }

  &--previous {
    left: 0;
    transform: rotate(180deg);
  }
}

